@import '../component/include';

/* **** Layout  **** */
#wrap {
    @include flex-layout($fd: column, $ai: normal);
    min-height: 100%;
    &[class ^= scroll] {
        #header {
            position: relative;
            border-bottom: .1rem solid var(--#{$prefix}gray-30);
            .header-in {
                position: fixed;
                top:0;
                left:0;
                z-index: 100;
                width: 100%;
                border-bottom: .1rem solid var(--#{$prefix}gray-30);
                background-color: var(--#{$prefix}white);
                transition: transform ease .4s .01s;
            }
        }
    }
    &.scroll-down {
        #header {
            .header-in {
                transform: translateY(-100%);
            }
        }
    }
    &.scroll-up{
        #header {
            .header-in {
                transform: translateY(0);
            }
        }
    }
}

.inner {
    width: 100%;
    max-width: 132.8rem;
    &.page-step {
		position: relative;
	}
}

#skip-nav {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    width: 100%;
    text-align: center;
    background-color: var(--#{$prefix}gray-90);
    a {
        &:not(:focus) {
            @include sr-only;
        }
        &:focus {
            @include flex-layout($ai: center, $jc: center,);
            @include focus($color: $white, $alpha: 0.9);
            font-size: 1.5rem;
            color:var(--#{$prefix}white);
            width: 100%;
            height: 3.2rem;
            margin: 0 auto;
            text-align: center;
        }
    }
    &.type1 {
        background-color: var(--#{$prefix}white);
        a {
            &:focus {
                color:var(--#{$prefix}gray-90);
            }
        }
    }
}


/* header */
#header-top { 
    position: relative;
    z-index: 70;
    background-color: var(--#{$prefix}secondary-5);
    .toggle-head {
        /*padding: 0 var(--#{$prefix}spacer-6);*/
        .inner {
            position: relative;
            gap: var(--#{$prefix}spacer-1) var(--#{$prefix}spacer-4);
            min-height: 3.2rem;
            padding-top: var(--#{$prefix}spacer-2);
            padding-left: calc(3.2rem + var(--#{$prefix}spacer-6));
            padding-right: var(--#{$prefix}spacer-6);
            padding-bottom: var(--#{$prefix}spacer-2);
        }
        .nuri-txt {
            @include flex-layout($ai: center);
            font-size: var(--#{$prefix}fz-body-sm);
            line-height: 1;
            word-break: break-all;
            &::before {
                display: inline-flex;
                position: absolute;
                left: var(--#{$prefix}spacer-6);
                content: '';
                width: 2.4rem;
                height: 1.6rem;
                margin-right:var(--#{$prefix}spacer-2);
                background: url(#{$url}/pattern/content/top_ban_ico_flag.svg) no-repeat center;
                background-size: contain;
            }
        }
        .toggle-btn {
            font-size: 1.4rem;
            color: var(--#{$prefix}secondary);
            height: auto;
            &::after {
                background-image: url(#{$url}/component/common/ico_arr_20_down_blue.svg);
                transition: $transition-base;
            }
        }
        &.active {
            .toggle-btn { 
                &::after {
                    transform: rotate(-180deg);
                }
            }
            ~ .toggle-body {
                display: block;
            }
        }
    }
    .toggle-body {
        height: 0;
        transition: $transition-base;
        overflow: hidden;
        &::before {
            display: flex;
            content: '';
            border-top: .1rem solid var(--#{$prefix}secondary-10);
        }
        .inner {
            padding-top: var(--#{$prefix}spacer-6);
            padding-bottom: var(--#{$prefix}spacer-6);
        }
        .dl {
            @include flex-layout($fd: column, $ai:normal);
            gap: var(--#{$prefix}spacer-4);
            flex: 1;
            .dt {
                display: flex;
                gap: var(--#{$prefix}spacer-2);
                align-items: center;
                font-weight: map-get($fws, bold);
                &::before {
                    display: inline-flex;
                    content:'';
                    width: 2rem;
                    height: 2rem;
                    background-image: url(#{$url}/pattern/layout/head_ico_nuri.svg);
                }
            }
            dd {
                padding-left: var(--#{$prefix}spacer-7);
            }
            .dd {
                font-size: var(--#{$prefix}fz-body-sm);
                color: var(--#{$prefix}gray-70);
            }
        }
    }
    .inner {
        @include flex-layout($ai: center);
        flex-wrap: wrap;
    }
} 

#header {
    position: sticky;
    top:0;
    left:0;
    z-index: 70;
    border-bottom: .1rem solid var(--#{$prefix}gray-30);
    background-color: var(--#{$prefix}white);
    .header-in {
        position: relative;
        transform: translateY(0);
    }

    .head-etc {
        @include flex-layout($jc: flex-end);
        .etc-ul {
            @include flex-layout($jc: flex-end);
            .li {
                @include flex-layout($ai: center);
                &:not(:first-child) {
                    gap: var(--#{$prefix}spacer-2);
                    margin-left: var(--#{$prefix}spacer-2);
                    &::before {
                        display: inline-flex;
                        content: '';
                        width: .1rem;
                        height: 1.6rem;
                        background-color: var(--#{$prefix}gray-30);
                    }
                }
            }
            .btn-txt {
                padding: 0 var(--#{$prefix}spacer-2);
            }
        }
    }

    .head-body {
        .inner {
            @include flex-layout($fd: column, $ai: normal);
        }
        .head-in {
            @include flex-layout($ai: center);
        }
        .logo {
            display: inline-flex;
            a {
                @include flex-layout($d:inline-flex, $ai: center);
                gap: var(--#{$prefix}spacer-8);
            }
        }
        .right {
            display: inline-flex;
            gap: var(--#{$prefix}spacer-2);
            margin-left: auto;
        }
        .btn-navi {
            @include flex-layout($d:inline-flex, $fd: column, $jc: space-between, $ai: center);
            gap: var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-2);
            padding: var(--#{$prefix}spacer-1) var(--#{$prefix}spacer-3);
            font-size: var(--#{$prefix}fz-body-md);
            font-weight: map-get($fws, bold);
            color:var(--#{$prefix}gray-90);
            line-height: 1;
            &::before {
                display: inline-flex;
                content: '';
                width: 2.4rem;
                height: 2.4rem;
                background-repeat: no-repeat;
                background-size: contain;
            }
            &:hover {
                border-radius: var(--#{$prefix}rd-6);
                background-color: var(--#{$prefix}secondary-5);
                transition: $transition-base;
            }
            &:active {
                background-color: var(--#{$prefix}secondary-10);
                transition: $transition-base;
            }
            &.navi-row {
                flex-direction: row;
            }
            &.sch {
                &::before {
                    background-image:url(#{$url}/pattern/layout/head_ico_navi_sch.svg);
                }
            }
            &.login {
                &::before {
                    background-image:url(#{$url}/pattern/layout/head_ico_navi_login.svg);
                }
            }
            &.join {
                &::before {
                    background-image:url(#{$url}/pattern/layout/head_ico_navi_join.svg);
                }
            }
            &.my {
                &::before {
                    background-image:url(#{$url}/pattern/layout/head_ico_navi_my.svg);
                }
            }
            &.all {
                &::before {
                    background-image:url(#{$url}/pattern/layout/head_ico_navi_all.svg);
                }
            }
        }
    }

    .head-gnb {
        border-top: .1rem solid var(--#{$prefix}gray-30);
        background-color: var(--#{$prefix}white);
        .gnb {
            @include flex-layout($jc: flex-start);
            gap: var(--#{$prefix}spacer-4);
            .li {
                a {
                    @include flex-layout($jc: flex-start, $ai: center);
                    gap: var(--#{$prefix}spacer-2);
                }
                .mn {
                    height: 6.2rem;
                    font-size: var(--#{$prefix}fz-body-lg);
                    font-weight: map-get($fws, bold);
                    color:var(--#{$prefix}gray-70);
                    padding: var(--#{$prefix}spacer-1) var(--#{$prefix}spacer-4) 0;
                    border-bottom: var(--#{$prefix}spacer-1) solid transparent;
                    &::after {
                        display: inline-flex;
                        content:'';
                        width: 2rem;
                        height: 2rem;
                        background: url(#{$url}/pattern/layout/head_gnb_ico_arr.svg) no-repeat center;
                        background-size: contain;
                    }
                    &.active {
                        color: var(--#{$prefix}gray-90);
                        background-color: var(--#{$prefix}secondary-10);
                        &::after {
                            transform: rotate(-180deg);
                        }
                    }
                    &:not(.active):hover {
                        color: var(--#{$prefix}secondary);
                        border-color: var(--#{$prefix}secondary);
                        background-color: var(--#{$prefix}white);
                        &::after {
                            background-image: url(#{$url}/pattern/layout/head_gnb_ico_arr_on.svg);
                        }
                    }
                }
                .mn-link {
                    &::after {
                        display: none;
                    }
                }
            }
        }
    }

    //웹 전체메뉴
    .w-gnb-wrap {
        display: none;
        position: absolute;
        top: calc(100% + .1rem);
        left:0;
        width: 100%;
        background-color: var(--#{$prefix}white);
        .ico-close {
            position: absolute;
            top: 1.6rem;
            right: 1.6rem;
            width: 3.2rem;
            height: 3.2rem;
            background-image: url(../../img/component/common/ico_close_24.svg);
        }
        .w-gnb-menu {
            display: flex;
            position: relative;
            background-color: var(--#{$prefix}secondary-5);
            .menu-wrap {
                @include flex-layout($fd:column, $ai: normal);
                position: relative;
                top: 0;
                left: 0; 
                gap: var(--#{$prefix}spacer-4);
                width: 26rem;
                padding: var(--#{$prefix}spacer-4) 0;
                .ul {
                    .sm {
                        @include flex-layout($ai: center);
                        gap: var(--#{$prefix}spacer-2);
                        font-weight: map-get($fws, bold);
                        width: 100%;
                        height: 5.6rem;
                        padding: 0 var(--#{$prefix}spacer-6);
                        &:hover {
                            background-color: var(--#{$prefix}white);
                            transition: $transition-base;
                        }
                        &:active {
                            background: var(--#{$prefix}secondary-10);
                            transition: $transition-base;
                        }
                        &.active {
                            color: var( --#{$prefix}secondary);
                            background-color: var(--#{$prefix}white);
                        }
                        &.ico-go {
                            &::after {
                                display: inline-flex;
                                content: '';
                                width: 2rem;
                                height: 2rem;
                                background-image: url(#{$url}/component/common/ico_go_20.svg);
                            }
                        }
                    }
                }
                .sub-ban {
                    padding: 0 var(--#{$prefix}spacer-4);
                    .ban-in {
                        @include flex-layout($fd:column,);
                        gap: var(--#{$prefix}spacer-2);
                        padding: var(--#{$prefix}spacer-4);
                        border-radius: .8rem;
                        background-color: var(--#{$prefix}white);
                    }
                }
            }

            .submenu-wrap {
                flex: 1;
                background-color: var(--#{$prefix}white);
                .submenu-in {
                    display: none;
                    padding: var(--#{$prefix}spacer-6) var(--#{$prefix}spacer-10);
                    &.active {
                        @include flex-layout($fd:column, $ai: normal);
                        gap: var(--#{$prefix}spacer-6);
                        &.between {
                            @include flex-layout($fd:row, $ai: normal);
                            height: 100%;
                            padding-right: 0;
                            .sub-ban {
                                @include flex-layout($fd:column, $ai: flex-start);
                                gap: var(--#{$prefix}spacer-2);
                                width: 26.2rem;
                                padding: var(--#{$prefix}spacer-6);
                            }
                            .sub-ul { 
                                grid-template-columns: 1fr 1fr 1fr;
                                &.type1 {
                                    grid-template-columns: 1fr 1fr;
                                }
                            }
                        }
                    }
                }
                .sub-tit {
                    @include flex-layout($fd:column, $jc: center, );
                    font-size: var(--#{$prefix}fz-title-xlg);
                    font-weight: map-get($fws, bold);
                    line-height: 5.6rem;
                    height: 5.6rem;
                    .btn-txt {
                        display: inline-flex;
                        font-size: var(--#{$prefix}fz-title-xlg);
                        font-weight: map-get($fws, bold);
                        padding:0 var(--#{$prefix}spacer-2) !important;
                    }
                    p {
                        &.btn-txt {
                            &:hover,
                            &:focus {
                                cursor: default;
                                background-color: transparent;
                            }
                        }
                    }
                }
                .sub-in {
                    @include flex-layout($fd:column, $ai: normal);
                    flex: 1;
                    gap: var(--#{$prefix}spacer-6);
                }
                .sub-ul {
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr 1fr;
                    grid-gap: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-4);
                    a {
                        @include flex-layout($jc:flex-start, $ai: center);
                        gap: var(--#{$prefix}spacer-1);
                        padding: var(--#{$prefix}spacer-2);
                        border-radius: .8rem;
                        &::before {
                            content: '･';
                            margin-top:.4rem;
                        }
                        &:hover {
                            background-color: var(--#{$prefix}secondary-5);
                        }
                        &:active {
                            background-color: var(--#{$prefix}secondary-10);
                        }
                        &:focus {
                            @include focus($color: $primary);
                        }
                        &[disabled] {
                            background-color: var(--#{$prefix}gray-30);
                            border-color: var(--#{$prefix}gray-40);
                            color: var(--#{$prefix}gray-60);
                        }
                    }
                    &.type1 {
                        grid-template-columns: 1fr 1fr 1fr;
                        grid-gap: var(--#{$prefix}spacer-10) var(--#{$prefix}spacer-14);
                        padding-bottom: var(--#{$prefix}spacer-4);
                        li {
                            @include flex-layout($fd:column, $jc:flex-start, $ai: normal);
                            padding: 0;
                            &::before { 
                                display: none;
                            }
                            .tit {
                                font-size: var(--#{$prefix}fz-body-lg);
                                font-weight: map-get($fws, bold);
                                a {
                                    @include flex-layout($jc:flex-start, $ai: center);
                                    height: 4rem;
                                    padding: 0 var(--#{$prefix}spacer-1);
                                    &::before {
                                        display: none;
                                    }
                                }
                            }
                            .txt {
                                color: var(--#{$prefix}gray-70);
                                padding: var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-1) 0;
                                margin-top: var(--#{$prefix}spacer-1);
                                border-top: .1rem solid var(--#{$prefix}gray-30);
                            }
                        }
                    }
                }
                .sub-ban {
                    @include flex-layout($ai: center);
                    gap: var(--#{$prefix}spacer-4);
                    padding: var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-4);
                    border-radius: var(--#{$prefix}rd-8);
                    background: var(--#{$prefix}gray-5);
                }
                &.type1 {
                    padding: var(--#{$prefix}spacer-4) 0 var(--#{$prefix}spacer-10);
                    border: 0;
                    .submenu-in {
                        @include flex-layout($fd:column, $ai: normal);
                        gap: var(--#{$prefix}spacer-4);
                        padding: 0;
                        &:not(.between) {
                            .sub-ul {
                                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                            }
                        }
                        &.between {
                            @include flex-layout($fd:row, $ai: normal);
                            gap: var(--#{$prefix}spacer-10);
                            .sub-in {
                                @include flex-layout($fd:column, $ai: normal);
                                flex: 1;
                                gap: var(--#{$prefix}spacer-4);
                            }
                            .sub-ban {
                                @include flex-layout($fd:column, $ai: flex-start);
                                gap: var(--#{$prefix}spacer-2);
                                width: 26.2rem;
                                padding: var(--#{$prefix}spacer-6);
                            }
                            
                        }
                    }
                    .sub-tit {
                        padding-bottom:  var(--#{$prefix}spacer-4);
                        border-bottom: .1rem solid var(--#{$prefix}gray-30);
                    }
                    
                }
            }
        }
        &.is-open {
            display: block;
        }
    }

    //모바일 전체메뉴
    .m-gnb-wrap {
        position: fixed;
        top: 0;
        right: -100%;
        z-index: 100;
        width: 100%;
        height: 100%;
        transition: right ease .5s .1s;
        &::after {
            display: none;
            content: '';
            position: fixed;
            top:0;
            right:0;
            width:100%;
            height: 100%;
            background-color: rgb(0, 0, 0, .5);
            transition: opacity .5s .1s;
        }
        .m-gnb-in {
            @include flex-layout($fd:column, $ai: normal);
            position: absolute;
            right:0;
            top:0;
            z-index: 5;
            height: 100%;
            background-color: var(--#{$prefix}white);
        }
        .ico-close {
            width: 2.4rem;
            height: 2.4rem;
            background-image: url(../../img/component/common/ico_close_24.svg);
        }
        .m-gnb-head {
            @include flex-layout($fd:column, $ai: normal);
            position: sticky;
            top: 0;
            left:0;
            gap: var(--#{$prefix}spacer-4);
            padding: var(--#{$prefix}spacer-5) var(--#{$prefix}spacer-6) var(--#{$prefix}spacer-4);
            .m-gnb-login {
                @include flex-layout($d:inline-flex, $ai: center);
                gap: var(--#{$prefix}spacer-2);
                .name {
                    font-weight: map-get($fws, bold);
                }
            }
            .btn-navi {
                @include flex-layout($d:inline-flex, $ai: center);
                gap: var(--#{$prefix}spacer-2);
                font-size: var(--#{$prefix}fz-body-md);
                height: 4rem;
                padding: 0;
                &::before {
                    display: inline-flex;
                    content: "";
                    width: 2rem;
                    height: 2rem;
                    background-repeat: no-repeat;
                    background-size: contain;
                }
                &:active {
                    border-radius: var(--#{$prefix}rd-6);
                    background-color: var(--#{$prefix}secondary-10);
                    transition: $transition-base;
                }
                &.login {
                    font-weight: map-get($fws, bold);
                    &::before {
                        background-image: url(../../img/pattern/layout/head_ico_navi_login.svg);
                    }
                }
            }
            .etc-menu {
                @include flex-layout($jc:space-between);
                gap: var(--#{$prefix}spacer-2);
                .link {
                    @include flex-layout($d: inline-flex, $fd:column, $ai: center);
                    gap: var(--#{$prefix}spacer-3);
                    font-size: var(--#{$prefix}fz-body-md);
                    font-weight: map-get($fws, bold);
                    width: 8rem;
                    height: 5.6rem;
                    &::before {
                        display: inline-flex;
                        content: '';
                        width: 2rem;
                        height: 2rem;
                        background-color: var(--#{$prefix}gray-20);
                    }
                    &:active {
                        border-radius: var(--#{$prefix}rd-6);
                        background-color: var(--#{$prefix}secondary-10);
                        transition: $transition-base;
                    }
                }
            }
        }

        .m-gnb-body {
            @include flex-layout($fd:column, $ai: normal);
            flex: 1;
            overflow-y: auto;
            scroll-behavior: smooth;
            border-top: .1rem solid var(--#{$prefix}gray-20);
            &.type1 {
                border:0;
            }
        }

        .m-gnb-body-btm {
            @include flex-layout($fd:column, $ai: flex-start);
            gap: var(--#{$prefix}spacer-2);
            padding: var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-6);
            border-top: .1rem solid var(--#{$prefix}gray-20);
        }

        .m-gnb-top-etc {
            @include flex-layout($jc:space-between);
            .etc-ul {
                display: flex;
                li {
                    @include flex-layout($ai: center);
                    &:not(:first-child) {
                        margin-left: var(--#{$prefix}spacer-4);
                        &::before {
                            display: inline-flex;
                            content: '';
                            width: .1rem;
                            height: 1.6rem;
                            margin-right: var(--#{$prefix}spacer-4);
                            background-color: var(--#{$prefix}gray-30);
                        }
                    }
                }
            }
        }

        .m-gnb-menu {
            display: flex;
            position: relative;
            background-color: var(--#{$prefix}secondary-5);
            .menu-wrap {
                @include flex-layout($fd:column);
                position: relative;
                top: 0;
                left: 0; 
                width: 12.4rem;
                .ul {
                    position: sticky;
                    top:0;
                    left: 0;
                    z-index: 1;
                    width: 100%;
                }
                .mn {
                    @include flex-layout($ai: center);
                    position: relative;
                    z-index: 1;
                    font-weight: map-get($fws, bold);
                    width: 100%;
                    height: 6.4rem;
                    padding: 0 var(--#{$prefix}spacer-6);
                    &.active {
                        color: var( --#{$prefix}secondary);
                        background-color: var(--#{$prefix}white);
                        &::after {
                            position: absolute;
                            top:0;
                            right: -1px;
                            content: '';
                            width: .1rem;
                            height: 100%;
                            background-color: var(--#{$prefix}white);
                        }
                    }
                }
            }
            .submenu-wrap {
                @include flex-layout($fd:column, $ai: normal);
                flex: 1;
                border-left: .1rem solid var(--#{$prefix}gray-20);
                background-color: var(--#{$prefix}white);
                .dl {
                    padding: var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-6);
                    &:not(:first-child) {
                        border-top: .1rem solid var(--#{$prefix}gray-20);
                    } 
                    dt {
                        @include flex-layout($ai: center);
                        position: relative;
                        font-weight: map-get($fws, bold);
                        height: 6.4rem;
                        margin-bottom: var(--#{$prefix}spacer-2);
                        border-bottom: .1rem solid var(--#{$prefix}gray-20);
                    }
                    .sm {
                        @include flex-layout($ai: center);
                        height: 5.6rem;
                        padding: 0 var(--#{$prefix}spacer-2);
                        border-radius: .8rem;
                        &:hover {
                            background-color: var(--#{$prefix}secondary-5);
                        }
                        &:active {
                            border-radius: var(--#{$prefix}rd-6);
                            background-color: var(--#{$prefix}secondary-10);
                            transition: $transition-base;
                        }
                    }
                    .sub-ul {
                        @include flex-layout($fd:column, $ai: normal);
                        gap: var(--#{$prefix}spacer-2);
                        .subm {
                            @include flex-layout($ai: center);
                            gap: var(--#{$prefix}spacer-1);
                            height: 4.8rem;
                            padding: var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-4);
                            border-radius: .8rem;
                            &::before {
                                content:'•';
                            }
                            &:hover {
                                background-color: var(--#{$prefix}secondary-5);
                            }
                            &:active {
                                border-radius: var(--#{$prefix}rd-6);
                                background-color: var(--#{$prefix}secondary-10);
                                transition: $transition-base;
                            }
                        }
                    }
                }
                .depth4-wrap {
                    display: none;
                    position: fixed;
                    top: 0;
                    right: -100%;
                    z-index: 10;
                    width: 100%;
                    height: 100%;
                    background-color:var(--#{$prefix}white);
                    transition: $transition-base;
                    .depth4-head {
                        @include flex-layout($jc:space-between, $ai:normal);
                        font-weight: map-get($fws, bold);
                        width: 100%;
                        height: 6.4rem;
                        padding: var(--#{$prefix}spacer-5) var(--#{$prefix}spacer-6);
                        .ico-prev {
                            display: inline-flex;
                            content: '';
                            width: 2.4rem;
                            height: 2.4rem;
                            background-image: url(../../img/pattern/layout/lnb_ico_prev.svg);
                            background-repeat: no-repeat;
                            background-size: contain;
                        }
                    }
                    .depth4-body {
                        dt {
                            height: 6.4rem;
                            padding: 0 var(--#{$prefix}spacer-6);
                            margin: 0;
                            border: 0;
                        }
                        .depth4-ul {
                            @include flex-layout($fd: column, $ai:normal);
                            gap: var(--#{$prefix}spacer-2);
                            padding: 0 var(--#{$prefix}spacer-6);
                            a {
                                @include flex-layout($ai: center);
                                gap: var(--#{$prefix}spacer-1);
                                height: 4.8rem;
                                padding: var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-4);
                                border-radius: .8rem;
                                &::before {
                                    content:'•';
                                    font-weight: map-get($fws, normal);
                                }
                                &:hover {
                                    font-weight: map-get($fws, bold);
                                    color: var(--#{$prefix}secondary);
                                    background-color: var(--#{$prefix}secondary-5);
                                }
                                &:active {
                                    border-radius: var(--#{$prefix}rd-6);
                                    background-color: var(--#{$prefix}secondary-10);
                                    transition: $transition-base;
                                }
                            }
                        }
                    }
                    &.is-open {
                        display: block;
                        right: 0;
                    }
                }
            }
            &.type1 {
                @include flex-layout($fd:column, $ai:normal);
                background-color: var(--#{$prefix}white);
                .submenu-wrap {
                    .dl {
                        padding: var(--#{$prefix}spacer-4) 0;
                        &:not(:last-child) {
                            border-bottom: .4rem solid var(--#{$prefix}gray-20);
                        }
                        .dt {
                            font-size: var(--#{$prefix}fz-body-lg);
                            height: 5.6rem;
                            padding: 0 var(--#{$prefix}spacer-6);
                        }
                        .sm {
                            padding: 0 var(--#{$prefix}spacer-6);
                        }
                        .subm {
                            padding : var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-10);
                        }
                        .depth4-wrap { 
                            a {
                                padding : var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-4);
                            }
                        }
                        .sub-ban {
                            padding: var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-6) 0;
                            .ban-in {
                                @include flex-layout($jc:flex-start, $ai:center,);
                                gap: var(--#{$prefix}spacer-2);
                                padding: 0 var(--#{$prefix}spacer-4);
                                border-radius: .8rem;
                                background-color: var(--#{$prefix}gray-5);
                            }
                        }
                    }
                }
            }
        }

        //모바일 gnb type2
        .m-gnb-in {
            &.type1 {
                &.is-active {
                    .m-gnb-head {
                        padding-bottom: 0;
                    }
                    .m-gnb-top-scroll {
                        display: block;
                    }
                }
                .m-gnb-head {
                    border-bottom: .4rem solid var(--#{$prefix}gray-20);
                }
            }
        }

        .m-gnb-top-scroll {
            display: none;
            height: 0;
            overflow: hidden;
        }

        .sch-input {
            + .menu-hori {
                margin-top: var(--#{$prefix}spacer-6);
            }
        }

        .menu-hori {
            padding: 0 var(--#{$prefix}spacer-6);
            margin:0  -2.4rem;
            overflow: hidden;
            .ul {
                @include flex-layout($fd:row,);
                gap: var(--#{$prefix}spacer-2);
                position: relative;
                padding-bottom: var(--#{$prefix}spacer-4);
                overflow-x: auto;
                scroll-behavior: smooth;
                .mn {
                    @include flex-layout($ai:center);
                    font-weight: map-get($fws, normal);
                    height: 3.6rem;
                    padding: 0 var(--#{$prefix}spacer-3);
                    border-radius: .6rem;
                    background-color: var(--#{$prefix}gray-10);
                    &.active {
                        background-color: var(--#{$prefix}secondary-10);
                    }
                }
            }
        }

        //모바일 gnb 영역 체크
        &.is-open {
            right:0;
            &::after {
                display: block;
                opacity: 1;
            }
        }
        &.is-close {
            &::after {
                display: block;
                opacity: 0;
            }
        }
    }
}

.w-gnb-dim {
