                border-top: .1rem solid var(--#{$prefix}gray-30);
                border-bottom: .1rem solid var(--#{$prefix}gray-30);
                a {
                    @include flex-layout($ai:center);
                    position: relative;
                    width: 100%;
                    height: 4.8rem;
                    padding: var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-8);
                    border-radius: .8rem;
                    &::before {
                        position: absolute;
                        left: 1.6rem;
