                    }
                    &.twitter {
                        background-image: url(#{$url}/pattern/layout/foot_ico_sns_twitter.svg);
                    }
                    &.facebook {
                        background-image: url(#{$url}/pattern/layout/foot_ico_sns_facebook.svg);
                    }
                    &.blog {
                        background-image: url(#{$url}/pattern/layout/foot_ico_sns_blog.svg);
                    }
                }
            }
        }
    }

    .f-btm {
        display: flex;
        flex-direction: column; 
        padding-top: var(--#{$prefix}spacer-2);
        gap: var(--#{$prefix}spacer-6);
        border-top: .1rem solid var(--#{$prefix}gray-30);
        .f-btm-text {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: var(--#{$prefix}spacer-4);
            .f-menu {
                display: inline-flex;
                flex-wrap: wrap;
                a {
                    display: inline-flex;
                    align-items: center;
                    height: 4rem;
                    padding: 0 1rem;
                    &.point {
                        font-weight: map-get($fws, bold);
                        color: var(--#{$prefix}secondary);
                    }
                    &:hover {
                        border-radius: var(--#{$prefix}rd-6);
                        background-color: var(--#{$prefix}secondary-5);
                        transition: $transition-base;
                    }
                    &:active {
                        background-color: var(--#{$prefix}secondary-10);
                        transition: $transition-base;
                    }

                    &:first-child {
                        padding-left: 0;
                    }
                }
            }
            .f-copy {
                font-size: var(--#{$prefix}fz-body-sm);
                color: var(--#{$prefix}gray-70);
            }
        }
        .f-btm-ban {
            display: flex;
            align-items: center;
            min-height: 4rem;
            padding: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-4);
            border-radius: var(--#{$prefix}rd-6);
            background-color: var(--#{$prefix}white);
            &::before {
                display: inline-flex;
                flex-shrink: 0;
                content: '';
                width: 7.2rem;
                height: 2.4rem;
                margin-right: var(--#{$prefix}spacer-2);
                background: url(#{$url}/pattern/content/btm_ban_ico_flag.svg) no-repeat center;
                background-size: contain;
            }
        }
        .f-btm-ban-txt {
            display: flex;
            align-items: center;
            font-size: var(--#{$prefix}fz-body-sm);
            line-height: 1;
            white-space: nowrap;
            .f-btm-text {
                display: flex;
            }
        }
    }
}

//web 전용 속성
