                display: none;
            }
           }
        }

        .m-gnb-wrap {
            display: none;
        }
    }

    #container {
        > .inner {
            padding: var(--#{$prefix}spacer-20) var(--#{$prefix}spacer-6);
        }
    }

    /* footer */
    #footer {
        > .inner {
            gap: var(--#{$prefix}spacer-10);
            padding: var(--#{$prefix}spacer-10) var(--#{$prefix}spacer-6);
        }

        .f-cnt {
            justify-content: space-between;
            flex: 1;
            .f-link {
                flex-shrink: 0;
                width: 30.2rem;
            }
        }
    }

}


//태블릿 전용 속성
@include tablet {
    /* header*/
    #header {
        .m-gnb-wrap {
            .m-gnb-in {
                width: 40%;
            }
            .m-gnb-menu {
                .submenu-wrap {
                    .depth4-wrap {
                        width: 40%;
                    }
                }
            }
        }
    }

    /* footer */
    #footer { 
        .foot-quick {
            .inner {
                .link {
                    font-size: 1.6rem;
                    line-height: 1.2;
                    word-break: break-all;
                    padding: 0 var(--#{$prefix}spacer-2) 0 var(--#{$prefix}spacer-4);
                    text-align: left;
                }
            }
        }
        .f-cnt {
            .f-info {
                flex: 3;
                gap: var(--#{$prefix}spacer-7);
                .info-cs {
                    gap: var(--#{$prefix}spacer-6);
                    li {
                        flex-direction: column;
                        align-items: flex-start;
                    }
                }
            }
        }
    }

}

//태블릿 & 모바일 전용 속성
@include tabletLess {
    .is-m-gnb {
        overflow: hidden;
    }

    .inner {
        padding-left: var(--#{$prefix}spacer-4);
        padding-right: var(--#{$prefix}spacer-4);
    }

    /* header */
    #header-top {
        .toggle-head {
            .inner {
                padding-left: calc(3.2rem + var(--#{$prefix}spacer-4));
                padding-right: var(--#{$prefix}spacer-4);
            }
            .nuri-txt {
                &::before {
                    left: var(--#{$prefix}spacer-4);
                }
            }
        }

    }
    #header {
        .inner {
            padding-top: var(--#{$prefix}spacer-3);
            padding-bottom: var(--#{$prefix}spacer-3);
        }
        
        .logo {
            .logo-im {
                height: 3.2rem;
            }
            .logo-slogan {
                display: none;
            }
        }

        .head-body {
            .head-etc {
                display: none;
            }
            .btn-navi {
                &.navi-row {
                    flex-direction: column;
                }
                &.join,
                &.my {
                    display: none;
                }
            }
        }

        .head-gnb {
            display: none;
        }

        .w-gnb-wrap {
            display: none !important;
        }
    }

    .w-gnb-dim {
        display: none !important;
    }

    /* container */
    #container {
        > .inner {
            padding-top: var(--#{$prefix}spacer-16);
            padding-bottom: var(--#{$prefix}spacer-16);
        }
        .in-between {
            .left-menu {
                display: none;
            }
            .contents {
                padding-left: var(--#{$prefix}spacer-4);
                padding-right: var(--#{$prefix}spacer-4);
            }
        }
    }

    /* footer */
    #footer { 
        > .inner {
            gap: var(--#{$prefix}spacer-8);
            padding: var(--#{$prefix}spacer-8) var(--#{$prefix}spacer-4);
        }
        .f-logo {
            height: 3.2rem;
        }
        .f-cnt {
            gap: var(--#{$prefix}spacer-8);
        }
        .f-btm {
            padding: var(--#{$prefix}spacer-8) var(--#{$prefix}spacer-4) 0;
            margin: 0 -1.6rem;
        }
    }

}

//모바일 전용
@include mobile {
    /* header */
    #header {
        .head-body {
            .btn-navi {
                gap: var(--#{$prefix}spacer-2);
                font-size: var(--#{$prefix}fz-label-xsm);
                padding: var(--#{$prefix}spacer-1);
                &::before {
                    width: 2rem;
                    height: 2rem;
                }
            }
        }

        //웹 전체메뉴
        .w-gnb-wrap {
            display: none !important;
        }

        //모바일 전체메뉴
        .m-gnb-wrap {
            .m-gnb-in {
                width: 100%;
            }
        }
    }

    /* container */
    #container {
        > .inner {
            min-height: 50rem;
        }
        .in-between {
            .contents {
                padding: 0 var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-10);
                .breadcrumb-wrap {
                    padding: var(--#{$prefix}spacer-6) 0 var(--#{$prefix}spacer-10);
                }
            }
        }
    }

     /* footer */
     #footer { 
        .foot-quick {
            > .inner {
                flex-direction: column;
                padding:0;
                .link {
                    font-size: var(--#{$prefix}fz-body-sm);
                    min-height: 4.8rem;
                    padding: 0 var(--#{$prefix}spacer-4);
                    border: 0;
                    &:first-child {
                        border:0;
                    }
                    &:not(:first-child) {
                        border-top: .1rem solid var(--#{$prefix}gray-30);
                    }
                }
            }
        }
        .f-cnt {
            flex-direction: column;
            .f-info {
                flex-direction: column;
                gap: var(--#{$prefix}spacer-4);
                font-size: var(--#{$prefix}fz-body-sm);
                .info-addr {
                    .br {
                        display: none;
                    }
                }
                .info-cs {
                    gap: var(--#{$prefix}spacer-4);
                    li {
                        display: block;
                        word-break: break-all;
                        .strong {
                            margin-right: var(--#{$prefix}spacer-1)
                        }
                    }
                }
            }
            .f-cs {
                gap: var(--#{$prefix}spacer-8);
                dd {
                    font-size: var(--#{$prefix}fz-body-sm);
                }
            }
            .f-link {
                gap: var(--#{$prefix}spacer-8);
                .link-go {
                    gap: var(--#{$prefix}spacer-4);
                    .btn-txt {
                        font-size: var(--#{$prefix}fz-body-sm);
                        height: auto;
                        padding: 0;
                        &::after {
                            width: 1.6rem;
                            height: 1.6rem;
                        }
                    }
                }
            }
        } 
        .f-btm {
            flex-direction: column;
            font-size: var(--#{$prefix}fz-body-sm);
            .f-btm-text {
                flex-direction: column;
                align-items: flex-start;
                width: 100%;
                .f-menu {
                    gap: var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-6);
                    a {
                        font-size: var(--#{$prefix}fz-body-sm);
                        height: auto;
                        padding: 0;
                    }
                }
            }
            .f-btm-ban {
                .ban-txt {
                    line-height: 1.1;
                    word-break: break-all;
                }
            }
        }
     }
}

/* 도움패널 레이아웃 2 */
@media (min-width: $breakpoint-md) and (max-width: 1900px) {
    #container {
        /*.breadcrumb-wrap {
            padding-left: 1.6rem;
            padding-right: 1.6rem;
        }*/
        &.help-open {
            padding-right: 43.5rem;
            .breadcrumb-wrap {
                justify-content: flex-start;
                padding-left:0;
                padding-right:0;
                .breadcrumb {
                    padding: 0;
                }
            }
            .inner {
                &.flexible { 
                    padding-left:0;
                    padding-right:0;
                }
            }
        }
        &.help-close {
            padding-right: 0rem;
            .breadcrumb-wrap {
                
                .breadcrumb {
                    padding: 0;
                }
            }
            .inner {
                &.flexible {
                    margin: 0 auto;
                }
            }
        }
        .inner {
            &.flexible {
                width: 100%;
                max-width: 1060px;
                margin-left: auto;
                margin-right: auto;
                transition: $transition-base;
                &.folded {
                    padding-left: 1.6rem;
                    padding-right: 1.6rem;
                }
            }
            &.folded {
                padding-right: 12rem;
            }
        }
    }
}